import React from 'react';
import './Footer.css';
// import { Button } from './react-native-button'; 
import { Link } from 'react-router-dom';

function Footer() {
  return (
    <div className='footer-container'>
      <section className='footer-subscription'>
        <p className='footer-subscription-heading'>
          Join the Developer Team to Receive Best Project Information
        </p>
        <p className='footer-subscription-text'>
          You can unsubscribe at any time.
        </p>
        <div className='input-areas'>
          <form>
            <input
              className='footer-input'
              name='email'
              type='email'
              placeholder='Your Email'
            />
            <button className="btn btn-primary">Subscribe</button>
            {/* <Button buttonStyle='btn--outline'>Subscribe</Button> */}
          </form>
        </div>
      </section>
      <div class='footer-links'>
        <div className='footer-link-wrapper'>
          <div class='footer-link-items'>
            <h2>About Us</h2>
            <a style={{color:"white"}} to='/'>How it works</a>
            <a style={{color:"white"}} to='/'>Testimonials</a>
            <a style={{color:"white"}} to='/'>Careers</a>
            <a style={{color:"white"}} to='/'>Investors</a>
            <a style={{color:"white"}} to='/'>Terms of Service</a>
          </div>
          <div class='footer-link-items'>
            <h2>Contact Us</h2>
            <a style={{color:"white"}} to='/'>Contact</a>
            <a style={{color:"white"}} to='/'>Support</a>
            <a style={{color:"white"}} to='/'>Destinations</a>
            <a style={{color:"white"}} to='/'>Sponsorships</a>
          </div>
        </div>
        <div className='footer-link-wrapper'>
          <div class='footer-link-items'>
            <h2>Videos</h2>
            <a style={{color:"white"}} to='/'>Submit Video</a>
            <a style={{color:"white"}} to='/'>Ambassadors</a>
            <a style={{color:"white"}} to='/'>Agency</a>
            <a style={{color:"white"}} to='/'>Influencer</a>
          </div>
          <div class='footer-link-items'>
            <h2>Social Media</h2>
            <a style={{color:"white"}} to='/'>Instagram</a>
            <a style={{color:"white"}} to='/'>Facebook</a>
            <a style={{color:"white"}} to='/'>Youtube</a>
            <a style={{color:"white"}} to='/'>Twitter</a>
          </div>
        </div>
      </div>
      <section class='social-media'>
        <div class='social-media-wrap'>
          <div class='footer-logo'>
            <a style={{color:"white"}} to='/' className='social-logo'>
            h1 Developer
              <i class='fab fa-typo3' />
            </a>
          </div>
          <small class='website-rights'>h1 Developer © copy rights Guruprasad Sali © 2021</small>
          <div class='social-icons'>
            <a style={{color:"white"}}
              class='social-icon-link facebook'
              to='/'
              target='_blank'
              aria-label='Facebook'
            >
              <i class='fab fa-facebook-f' />
            </a> 
             {/* <Link
              class='social-icon-link instagram'
              to='/'
              target='_blank'
              aria-label='Instagram'
            >
              <i class='fab fa-instagram' />
            </Link>
            <Link
              class='social-icon-link youtube'
              to='/'
              target='_blank'
              aria-label='Youtube'
            >
              <i class='fab fa-youtube' />
            </Link>
            <Link
              class='social-icon-link twitter'
              to='/'
              target='_blank'
              aria-label='Twitter'
            >
              <i class='fab fa-twitter' />
            </Link>
            <Link 
              class='social-icon-link twitter'
              to='/'
              target='_blank'
              aria-label='LinkedIn'
            >
              <i class='fab fa-linkedin' />
            </Link> */}
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;