import React, {useState} from 'react'

export default function NavBar() {

  const [btn, setBtn] = useState("About App")


    return (
        <div>


            <nav style={{color:"white"}}  className="navbar navbar-expand-lg navbar-light  bg-dark" style={{color:"white"}}>
  <div className="container-fluid" style={{color:"white"}}>
    <a className="navbar-brand" href="/" style={{color:"white"}}>TextEditor</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" style={{backgroundColor:"white"}}></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="/" style={{color:"white"}}>Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link"  href="/"  style={{color:"white"}}>{btn}</a>
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" style={{color:"white"}} href="/" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Dropdown
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown" style={{color:"white"}}>
            <li><a className="dropdown-item" href="/">Action</a></li>
            <li><a className="dropdown-item" href="/">Another action</a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" href="/">Something else here</a></li>
          </ul>
        </li>
      </ul>
      <form className="d-flex" style={{color:"white"}}>
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" style={{color:"white"}} type="submit">Search</button>
      </form>
    </div>
  </div>
</nav>
        </div>
    )
}









