import React, {useState} from 'react'

export default function About() {

    const [myStyle, setMyStyle] = useState( {
        color : "white",
        backgroundColor : "black",
        border : "solid 1 px"
    }    )

    const [btnstyle, setBtnSyle] = useState("Enable Dark Mode")

    const togglebtn = () => {
        if(myStyle.color === "white"){
            setMyStyle({
                color : "black",
        backgroundColor : "white",
        border : "solid 1 px"
            })
            setBtnSyle("Enable Dark Mode")
        }
        else{
            setMyStyle({
                color : "white",
        backgroundColor : "black",
        border : "solid 1 px"
            })
            setBtnSyle("Enable Light Mode")
            

        }
    }






    return (
        <div className="container" style={myStyle}>
        <h1>About Us</h1>
            
            <div class="accordion" id="accordionExample">
  <div class="accordion-item">
    <h2 class="accordion-header" id="headingOne">
      <button class="accordion-button" style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
        What does this app do?
      </button>
    </h2>
    <div id="collapseOne" class="accordion-collapse collapse show" style={myStyle} aria-labelledby="headingOne" data-bs-parent="#accordionExample">
      <div class="accordion-body">
        <strong>This react application help you to count the word and character also it will convert your text into lower case and upper case whatever you want you can edit it and also copy this converted text to your clipboard i hope it will give you better user interface </strong>
      </div>
    </div>
  </div>
  <div class="accordion-item">
    <h2 class="accordion-header" id="headingTwo">
      <button class="accordion-button collapsed" style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
        Info about Developer
      </button>
    </h2>
    <div id="collapseTwo" class="accordion-collapse collapse" style={myStyle} aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
      <div class="accordion-body">
        <strong>Guruprasad is a Developer of this app</strong> if you want to build your own application please contact on following whatsapp number or DM on instagram we both create superb app together so... what are you wating for just call  <code>8390738209</code>, and get your own website/apps
      </div>
    </div>
  </div>
  <div class="accordion-item">
    <h2 class="accordion-header" id="headingThree">
      <button class="accordion-button collapsed" style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
        Contact info
      </button>
    </h2>
    <div id="collapseThree" class="accordion-collapse collapse" style={myStyle} aria-labelledby="headingThree" data-bs-parent="#accordionExample">
      <div class="accordion-body">
        <strong>Name: Guruprasad Sali</strong><br/> Email:  <code>guruprasadsali378@gmail.com</code>,<br/> Contact: <code>8390738209</code>, <br/>LinkedIn : <code>https://www.linkedin.com/in/guruprasad-sali-a26a74194</code>, <br/> 
      </div>
    </div>
  </div>
</div>
        <button className="btn btn-danger mx-2"  onClick={togglebtn}> {btnstyle}</button>

        </div>
    )
}
