import React, { useState } from "react";

export default function TextForm(props) {

    const [myStyle, setMyStyle] = useState( {
        color : "black",
        backgroundColor : "white",
        border : "solid 1 px"
    }    )

    const [btnstyle, setBtnSyle] = useState("Enable Dark Mode")

    const togglebtn = () => {
        if(myStyle.color === "white"){
            setMyStyle({
                color : "black",
        backgroundColor : "white",
        border : "solid 1 px"
            })
            setBtnSyle("Enable Dark Mode")
            document.body.style.backgroundColor = "black"
        }
        else{
            setMyStyle({
                color : "white",
        backgroundColor : "black",
        border : "solid 1 px"
            })
            setBtnSyle("Enable Light Mode")
            

        }
    }

    


  const [text, setText] = useState("Enter your text here");

  const handleChange = (event) => {
    console.log("handle change even fire");
    setText(event.target.value);
  };

  const handleClick = () => {
    const newText = text.toUpperCase();
    console.log("handle click event is fire");
    setText(newText);
  };

  const clickMe = () => {
    const lower = text.toLowerCase();
    setText(lower);
  };

  const handleCopy = () => {
      var text = document.getElementById("mybox")
      text.select()
      navigator.clipboard.writeText(text.value)
    //   setTimeout(() => {
    //       alert(" msg : Text copid to clipboard")
    //   },1500)
    // tempAlert("close",2000);
    var w = window.open('','','width=200,height=100')
    w.document.write('Success: Text copied')
    w.focus()
    setTimeout(function() {w.close();}, 2000)
  }
  const handleSpace = () => {
      let newText = text.split(/[ ]+/);
      setText(newText.join(" "));

  }

  return (
    <div style={myStyle}>
      <div className="container" style={myStyle}>
      <button className="btn btn-danger mx-2" style={{float:"right"}} onClick={togglebtn}> {btnstyle}</button>
        <h1>Enter your text here {props.texts}</h1> 

        <div className="mb-3" style={myStyle}>
          <textarea
            className="form-control mx-2"  style={myStyle}
            value={text}
            onChange={handleChange}
            id="mybox"
            rows="8"
          >
            {" "}
            Enter text
          </textarea>
          <div style={{ float: "left" }} className="my-2" >
            <button className="btn btn-warning mx-2"  onClick={handleClick}>
              Convert to UpperCase
            </button>
            &nbsp;
            <button className="btn btn-success mx-2"  onClick={clickMe}>
              Convert to lowerCase
            </button>
            <button className="btn btn-info mx-2"  onClick={handleCopy}>
              Copy Text
            </button>
            <button className="btn btn-primary mx-2"  onClick={handleSpace}>
              Remove Extra Space
            </button>

            
          </div>
        </div>
      </div>

      <div className="container" style={myStyle} >
      <br/><br/>
        <h2>Your text summery</h2>
        <h4>
          {" "}
          {text.split(" ").length -1} words and {text.length} character
        </h4>
        <h4>{0.008 * text.split(" ").length} Miniute to read</h4>
        <h4>Preview</h4>
        <p>{text}</p>
      </div>
    </div>
  );
}
